import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import './Contact.scss'
import Swal from 'sweetalert2'
// framer motion
import { motion } from 'framer-motion';
import { fadeIn } from '../../utils/variants';

const Contact = () => {
    const form = useRef();

    // succesful message sent notif
    const Succes = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 4000,
        timerProgressBar: true,
        didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
    })

    // error message sent notif
    const Error = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 4000,
        timerProgressBar: true,
        didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
    })

    // Contact - send emails
    const sendEmail = (e) => {
        e.preventDefault();
        emailjs.sendForm('service_tc21d2l', 'template_vxfs4jc', form.current, '-9wYTINsAMAraDv4O')
            .then((result) => {
                Succes.fire({ // succesful message sent notif
                    icon: 'success',
                    title: 'Your message has been sent 😎'
                })
                e.target.reset();
            }, (error) => {
                Error.fire({ // error message sent notif
                    icon: 'error',
                    title: 'Something is not working 🤔',
                })
            });
    };

    return (
        <section className="contact section" id="contact">
            <motion.h2 
                className="section-title"
                variants={fadeIn('right')}
                initial='hidden'
                whileInView={'show'}
                viewport={{ once: false, amount: 0.5 }}
            >
                Get in touch
            </motion.h2>
            <motion.span 
                className="section-subtitle"
                variants={fadeIn('left')}
                initial='hidden'
                whileInView={'show'}
                viewport={{ once: false, amount: 0.5 }}
            >
                - Contact Me -
            </motion.span>

            <div className="contact-container container grid">
                <div className="contact-content">
                    <motion.h3 
                        className="contact-title"
                        variants={fadeIn('right')}
                        initial='hidden'
                        whileInView={'show'}
                        viewport={{ once: false, amount: 0.5 }}
                    >
                        Talk to me
                    </motion.h3>

                    <div className="contact-info">
                        <motion.div 
                            className="contact-card"
                            variants={fadeIn('right')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            <i className="bx bx-mail-send contact-card-icon"></i>
                            <h3 className="contact-card-title">Email</h3>
                            <span className="contact-card-data">1panescu.cosmin@gmail.com</span>
                            <a href="mailto: 1panescu.cosmin@gmail.com" target="_blank"  rel="noreferrer" className="contact-button">
                                Write me {" "}
                                <i className="bx bx-right-arrow-alt contact-button-icon"></i>
                            </a>
                        </motion.div>
                        <motion.div 
                            className="contact-card"
                            variants={fadeIn('right')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            <i className="bx bxl-whatsapp contact-card-icon"></i>
                            <h3 className="contact-card-title">Whatsapp</h3>
                            <span className="contact-card-data">0799 526 060</span>
                            <a href="https://wa.me/40799526060" target="_blank"  rel="noreferrer" className="contact-button">
                                Write me {" "}
                                <i className="bx bx-right-arrow-alt contact-button-icon"></i>
                            </a>
                        </motion.div>
                        <motion.div 
                            className="contact-card"
                            variants={fadeIn('right')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            <i className="bx bxl-messenger contact-card-icon"></i>
                            <h3 className="contact-card-title">Messenger</h3>
                            <span className="contact-card-data">Cosmin Pănescu
                            </span>
                            <a href="https://m.me/cosminpane"  rel="noreferrer" target="_blank" className="contact-button">
                                Write me {" "}
                                <i className="bx bx-right-arrow-alt contact-button-icon"></i>
                            </a>
                        </motion.div>
                    </div>
                </div>

                <div className="contact-content">
                    <motion.h3 
                        className="contact-title"
                        variants={fadeIn('left')}
                        initial='hidden'
                        whileInView={'show'}
                        viewport={{ once: false, amount: 0.5 }}
                    >
                        Write me your ideas
                    </motion.h3>

                    <form className="contact-form" ref={form} onSubmit={sendEmail}>
                        <motion.div 
                            className="contact-form-div"
                            variants={fadeIn('left')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            <label className="contact-form-tag">Name</label>
                            <input
                                required
                                type="text"
                                className="contact-form-input"
                                name='name'
                                placeholder='Insert your name'
                            />
                        </motion.div>
                        <motion.div 
                            className="contact-form-div"
                            variants={fadeIn('left')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            <label className="contact-form-tag">Email</label>
                            <input
                                required
                                type="email"
                                className="contact-form-input"
                                name='email'
                                placeholder='Insert your email'
                            />
                        </motion.div>
                        <motion.div 
                            className="contact-form-div contact-form-area" 
                            variants={fadeIn('left')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            <label className="contact-form-tag">Ideas</label>
                            <textarea
                                required
                                name="message"
                                cols="30" rows="10"
                                className='contact-form-input'
                                placeholder='Write here your ideas'>
                            </textarea>
                        </motion.div>

                        <motion.button 
                            className="button button-flex"
                            variants={fadeIn('left')}
                            initial='hidden'
                            whileInView={'show'}
                            viewport={{ once: false, amount: 0.5 }}
                        >
                            Send Message
                            <svg
                                className="button-icon"
                                xmlns="http://www.w3.org/2000/svg"
                                width="24"
                                height="24"
                                viewBox="0 0 24 24"
                                fill="none"
                            >
                                <path
                                    d="M14.2199 21.9352C13.0399 21.9352 11.3699 21.1052 10.0499 17.1352L9.32988 14.9752L7.16988 14.2552C3.20988 12.9352 2.37988 11.2652 2.37988 10.0852C2.37988 8.91525 3.20988 7.23525 7.16988 5.90525L15.6599 3.07525C17.7799 2.36525 19.5499 2.57525 20.6399 3.65525C21.7299 4.73525 21.9399 6.51525 21.2299 8.63525L18.3999 17.1252C17.0699 21.1052 15.3999 21.9352 14.2199 21.9352ZM7.63988 7.33525C4.85988 8.26525 3.86988 9.36525 3.86988 10.0852C3.86988 10.8052 4.85988 11.9052 7.63988 12.8252L10.1599 13.6652C10.3799 13.7352 10.5599 13.9152 10.6299 14.1352L11.4699 16.6552C12.3899 19.4352 13.4999 20.4252 14.2199 20.4252C14.9399 20.4252 16.0399 19.4352 16.9699 16.6552L19.7999 8.16525C20.3099 6.62525 20.2199 5.36525 19.5699 4.71525C18.9199 4.06525 17.6599 3.98525 16.1299 4.49525L7.63988 7.33525Z"
                                    fill="#fff"
                                ></path>
                                <path
                                    d="M10.11 14.7052C9.92005 14.7052 9.73005 14.6352 9.58005 14.4852C9.29005 14.1952 9.29005 13.7152 9.58005 13.4252L13.16 9.83518C13.45 9.54518 13.93 9.54518 14.22 9.83518C14.51 10.1252 14.51 10.6052 14.22 10.8952L10.64 14.4852C10.5 14.6352 10.3 14.7052 10.11 14.7052Z"
                                    fill="#fff"
                                ></path>
                            </svg>
                        </motion.button>
                    </form>
                </div>
            </div>
        </section >
    )
}

export default Contact